    <div class="main">
        <div v-wechat-title="$route.meta.title"></div>
        <!-- 产品列表 -->
        <!--<div class="typeTitle">
            <img src="./img/hot.png" alt="">
            <span>热卖产品</span>
        </div>-->
        <div ref="swiper-container" class="swiper-container swiper-main" v-if="banner.length>0">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,index) in banner" :key="index">
                    <img v-if="item.bannerImage" :src="item.bannerImage" @click="goDetail('/productDetail?id='+item.bannerJoinId)" alt="">
                    <img v-if="!item.bannerImage" src="./img/icon_empty_1.png" alt="">
                </div>
            </div>
            <div ref="swiper-pagination" class="swiper-pagination"></div>
        </div>
        <div class="product-wrap">
            <div class="product-item" @click="goDetail('/productDetail?id='+item.productId)"
                 v-for="(item,index) in productList" :key="index">
                <div class="productImg">
                    <img v-if="item.img" :src="item.img" alt="">
                    <img v-if="!item.img" src="./img/icon_empty_1.png" alt="">
                </div>
                <div class="productTitle">{{item.productName}}</div>
                <div class="productPrice">￥{{Number(item.productPrice).toFixed(2)}}</div>
                <!-- <div class="productTitle">{{item.productDescribe}}</div>-->

            </div>
        </div>
    </div>
