<script>
    import request from '../../../static/js/request';
    import result from '../../../static/js/result';
    import Swiper from '../../../static/js/swiper/swiper';
    export default {
        data() {
            return {
                banner: [],
                productList: [],
                showA: true,
                showB: false,
                showC: false,
                isShowBanner:false
            }
        },
        mounted() {

        },
        created() {
            console.log('域名',this.$host)
            this.getBanner();
            this.getList();
        },
        methods: {
            VIEW(item) {
                console.log(item)
            },
            /**
             * 查看详情
             * **/
            goDetail(url) {
                this.$router.push(url);
            },
            /**
             * 获取banner图
             * **/
            async getBanner() {
                this.banner = []
                let params = {

                };
                let getListInfo = await request('/api/open/banner/selectBaseBannerList',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(getListInfo)
                console.log(res)
                // loading.hide()
                if (!res) return;
                console.log('返回结果', getListInfo)
                /*在banner图时间内并且bannerState为0的显示*/
                for(let item of getListInfo.data){
                    if(item.bannerStartTime&&item.bannerEndTime&&item.bannerState==0){
                        this.getNowDate(item.bannerStartTime,item.bannerEndTime)
                        //this.$nextTick(()=>{
                        item.isShowBanner = this.isShowBanner;
                        if(item.isShowBanner){
                            this.banner.push(item)
                        }
                        // })

                    }else if(item.bannerState==0){
                        this.banner.push(item)
                    }
                }
                console.log(this.banner)
                //this.banner = getListInfo.data
                this.$nextTick(()=>{
                    new Swiper(this.$refs['swiper-container'],{
                        pagination: {
                            el: this.$refs['swiper-pagination'],
                            clickable :true
                        },
                        autoplay: {
                            delay: 3000,
                            stopOnLastSlide: false,
                            disableOnInteraction: false,
                        },
                    })
                })
            },
            /**
             * 判断时间是否在有效期
             * **/
            getNowDate(st,ed){
                let date = new Date();
                let seperator1 = "-";
                let seperator2 = ":";
                let month = date.getMonth() + 1;
                let strDate = date.getDate();
                let hours = date.getHours(); //获取当前小时数(0-23)
                let minutes = date.getMinutes(); //获取当前分钟数(0-59)
                let seconds = date.getSeconds(); //获取当前秒数(0-59)
                if (month >= 1 && month <= 9) {
                    month = "0" + month;
                }
                if (strDate >= 0 && strDate <= 9) {
                    strDate = "0" + strDate;
                }
                let nowDate = date.getFullYear() + seperator1 + month + seperator1 + strDate +' '+ hours + seperator2 + minutes + seperator2 + seconds;
                console.log(nowDate)
                let endDate = new Date(ed);
                let startDate = new Date(st);

                // 有三种方式获取
                let nowDate1 = new Date(nowDate);

                let time = startDate.getTime();
                let time1 = endDate.getTime();
                let time2 = nowDate1.getTime();
                console.log('当前日期',time2,'开始时间',time,'结束时间',time1)
                if((Number(time2)-Number(time1))<0&&(Number(time2)-Number(time))>0){

                    this.isShowBanner = true
                }else{
                    this.isShowBanner = false
                }
            },
            /**
             * 获取产品列表
             * **/
            async getList() {

                let params = {
                    productState: 2
                };
                let getListInfo = await request('/api/open/product/list',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(getListInfo)
                console.log(res)
                // loading.hide()
                if (!res) return;
                console.log('返回结果', getListInfo)
                this.productList = getListInfo.data;
                for (let item of this.productList) {
                    if (item.productImgs.length > 0) {
                        if (item.productImgs[0].imgUrl) {
                            item.img = item.productImgs[0].imgUrl
                        } else {
                            item.img = ''
                        }
                    } else {
                        item.img = ''
                    }
                }
            },
            tabNav() {
                weui.tab('#tab', {
                    defaultIndex: 0,
                    onChange: function (index) {
                        console.log(index);
                    }
                });
            },
            chaxun(e) {
                console.log(e);
                if (e == 'showA') {
                    this.showA = true;
                    this.showB = false;
                    this.showC = false;
                } else if (e == 'showB') {
                    this.showB = true;
                    this.showA = false;
                    this.showC = false
                } else if (e == 'showC') {
                    this.showC = true;
                    this.showA = false;
                    this.showB = false;
                }
            }
        }
    }
</script>

